import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
    // 
    const VueRouterPush = VueRouter.prototype.push
    VueRouter.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
    }
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
import beifen from '@/views/modules/databaseBackup/beifen'
import huanyuan from '@/views/modules/databaseBackup/huanyuan'

     import users from '@/views/modules/users/list'
    import chat from '@/views/modules/chat/list'
    import dictionary from '@/views/modules/dictionary/list'
    import feipin from '@/views/modules/feipin/list'
    import feipinOrder from '@/views/modules/feipinOrder/list'
    import gonggao from '@/views/modules/gonggao/list'
    import liuyan from '@/views/modules/liuyan/list'
    import singleSeach from '@/views/modules/singleSeach/list'
    import yonghu from '@/views/modules/yonghu/list'
    import config from '@/views/modules/config/list'
    import dictionaryChat from '@/views/modules/dictionaryChat/list'
    import dictionaryFeipin from '@/views/modules/dictionaryFeipin/list'
    import dictionaryFeipinOrder from '@/views/modules/dictionaryFeipinOrder/list'
    import dictionaryGonggao from '@/views/modules/dictionaryGonggao/list'
    import dictionarySex from '@/views/modules/dictionarySex/list'
    import dictionarySingleSeach from '@/views/modules/dictionarySingleSeach/list'
    import dictionaryZhuangtai from '@/views/modules/dictionaryZhuangtai/list'





//2.   
const routes = [{
    path: '/index',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }, {
        path: '/huanyuan',
        name: '',
        component: huanyuan
    }, {
        path: '/beifen',
        name: '',
        component: beifen
    }, {
        path: '/users',
        name: '',
        component: users
    }
    ,{
        path: '/dictionaryChat',
        name: '',
        component: dictionaryChat
    }
    ,{
        path: '/dictionaryFeipin',
        name: '',
        component: dictionaryFeipin
    }
    ,{
        path: '/dictionaryFeipinOrder',
        name: '',
        component: dictionaryFeipinOrder
    }
    ,{
        path: '/dictionaryGonggao',
        name: '',
        component: dictionaryGonggao
    }
    ,{
        path: '/dictionarySex',
        name: '',
        component: dictionarySex
    }
    ,{
        path: '/dictionarySingleSeach',
        name: '',
        component: dictionarySingleSeach
    }
    ,{
        path: '/dictionaryZhuangtai',
        name: '',
        component: dictionaryZhuangtai
    }
    ,{
        path: '/config',
        name: '',
        component: config
    }


    ,{
        path: '/chat',
        name: '',
        component: chat
      }
    ,{
        path: '/dictionary',
        name: '',
        component: dictionary
      }
    ,{
        path: '/feipin',
        name: '',
        component: feipin
      }
    ,{
        path: '/feipinOrder',
        name: '',
        component: feipinOrder
      }
    ,{
        path: '/gonggao',
        name: '',
        component: gonggao
      }
    ,{
        path: '/liuyan',
        name: '',
        component: liuyan
      }
    ,{
        path: '/singleSeach',
        name: '',
        component: singleSeach
      }
    ,{
        path: '/yonghu',
        name: '',
        component: yonghu
      }


    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '',
    redirect: '/index'
  }, /**/
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})

export default router;
